.msg {
  padding: 10px;
  background: #222;
  position: relative;
}
.msg h1 {
  color: #fff;
}
.msg a {
  margin-left: 20px;
  background: #408814;
  color: white;
  padding: 4px 8px;
  text-decoration: none;
}
.msg a:hover {
  background: #266400;
}

/* //-- -u-grids style overrides -- */
/* body { font-family: Georgia; color: #444; } */
#inner {
  padding: 10px 80px;
  margin: 80px auto;
  background: #f5f5f5;
  border: solid #666;
  border-width: 8px 0 2px 0;
}
.yui-gf {
  margin-bottom: 2em;
  padding-bottom: 2em;
  border-bottom: 1px solid #ccc;
}

/* //-- header, body, footer -- */
#hd {
  margin: 2.5em 0 3em 0;
  padding-bottom: 1.5em;
  border-bottom: 1px solid #ccc;
}
#hd h2 {
  text-transform: uppercase;
  letter-spacing: 2px;
}
#bd,
#ft {
  margin-bottom: 2em;
}

/* //-- footer -- */
#ft {
  padding: 1em 0 5em 0;
  font-size: 92%;
  border-top: 1px solid #ccc;
  text-align: center;
}
#ft p {
  margin-bottom: 0;
  text-align: center;
}

/* //-- core typography and style -- */
#hd h1 {
  font-size: 48px;
  text-transform: uppercase;
  letter-spacing: 3px;
}
.h2Heading {
  font-size: 152%;
  color: #333;
}
.h3_4Heading {
  font-size: 122%;
  color: #333;
}
.h1Color {
  color: #333;
}
/* h1, h2, h3, h4 { color: #333; } */
.Pstyle {
  font-size: 100%;
  line-height: 18px;
  padding-right: 3em;
}
.aStyle {
  color: #990003;
}
.aStyle:hover {
  text-decoration: none;
}
strong {
  font-weight: bold;
}
.liStyle {
  line-height: 24px;
  border-bottom: 1px solid #ccc;
}
.Pstyle .enlarge {
  font-size: 144%;
  padding-right: 6.5em;
  line-height: 24px;
}
.Pstyle .enlarge span {
  color: #000;
}
.contact-info {
  margin-top: 7px;
}
.first h2 {
  font-style: italic;
}
.last {
  border-bottom: 0;
}

/* //-- section styles -- */

.aStyle#pdf {
  display: block;
  float: left;
  background: #666;
  color: white;
  padding: 6px 50px 6px 12px;
  margin-bottom: 6px;
  text-decoration: none;
}
.aStyle#pdf:hover {
  background: #222;
}

.job {
  position: relative;
  margin-bottom: 1em;
  padding-bottom: 1em;
  border-bottom: 1px solid #ccc;
}
.job h4 {
  position: absolute;
  top: 0.35em;
  right: 0;
}
.job p {
  margin: 0.75em 0 3em 0;
}

.last {
  border: none;
}
.skills-list {
}
.skills-list ul {
  margin: 0;
}
.skills-list li {
  margin: 3px 0;
  padding: 3px 0;
}
.skills-list li span {
  font-size: 152%;
  display: block;
  margin-bottom: -2px;
  padding: 0;
}
.talent {
  width: 32%;
  float: left;
}
.talent h2 {
  margin-bottom: 6px;
}

#srt-ttab {
  margin-bottom: 100px;
  text-align: center;
}
#srt-ttab img.last {
  margin-top: 20px;
}

/* --// override to force 1/8th width grids -- */
.yui-gf .yui-u {
  width: 80.2%;
}
.yui-gf div.first {
  width: 12.3%;
}

/* my css  */
.Resume {
  width: 60%;
  background-color: white;
  margin: auto;
  margin-top: -5rem;
  border-radius: 7px;
}

/* utilities */

.removeBottom {
  border: none;
}

.addBottom {
  border-bottom: 4px solid black;
  margin-bottom: 1rem;
  border-length: 100%;
}

.addMarginBottom {
  margin-bottom: 4rem;
}

.mailHeading {
  margin-right: 2rem;
}

/* utilites end */

.skillContainer {
  display: flex;
  justify-content: space-around;
  /* margin-bottom: 1rem; */
}

.skillContainer ul li {
  border: none;
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
  text-transform: capitalize;
  font-weight: border;
}
